import React, { useState, useEffect } from "react";
import axios from "axios";
import Typography from '@material-ui/core/Typography';

function GameDesc(props) {
    const CORS_API_HOST = "https://cors-anywhere.herokuapp.com/";
    const API_BASE_URL = "https://api.rawg.io/api/games"
    const API_KEY = "b9c7fa3fd2144d7881237685a739a38f"

    const [desc, setDesc] = useState({});

    useEffect(() => {
        const API_URL = `${CORS_API_HOST}${API_BASE_URL}/${props.match.params.id}?key=${API_KEY}`
        axios.get(API_URL)
        .then(res => {
            setDesc(res.data);
        })
        .catch(err => console.log(err));
        console.log(process.env.API_ROUTE)
    }, [props.match.params.id])

    return (
        <div className="game-desc">

            <h1>{desc.name}</h1>
            <div className="rating-text">
                Rating: {desc.rating}/5
            </div>

            <img src={desc.background_image} alt={desc.name} />

            <Typography variant="body1" color="initial" component="p" dangerouslySetInnerHTML={{__html: desc.description}}></Typography>

            <div className="visit-website">
                <h2>Visit website: </h2>
                <a href={desc.website}>{desc.website}</a>
            </div>
        </div>
    )
}

export default GameDesc;